/* ==========================================================================
   Advanced Inputs

   Advanced and combined Mendix & Apollo input elements
========================================================================== */
// used for combining input fields with buttons
.combined-form-group {
    position: relative;
}

.form-control-toolbar {
    position: absolute;
    right:1px;
    top: calc(1px + (1em * #{$line-height-base}) + #{$spacing-smaller});

    .btn {
        @extend %panel-btn;
        border:none;
    }
}

%horizontal-combined-form-group,
.horizontal-combined-form-group {
    .form-control-toolbar {
        top: 1px;
    }
}

.combined-form-group-nolabel {
    .form-control-toolbar {
        top: 0;
    }
}

// smaller version of a search input building block
.compact-search-group {
    @extend %horizontal-combined-form-group;
    position: relative;
    
    @if ($dense-app) {
        padding-right: calc(2 * (#{$spacing-small} + 5px) + 1em);
    } @else {
        padding-right: calc(2 * (#{$spacing-small} + 5px + 5px) + 1em); 
    }

    .form-control-toolbar {
        @if ($dense-app) {
            right: calc(2 * (#{$spacing-small} + 5px) + 1em + 1px);
        } @else {
            right: calc(2 * (#{$spacing-small} + 5px + 5px) + 1em + 1px); 
        }
    }

    .search-clear-btn {
        position: absolute;
        color: #FFF;

        @if ($dense-app) {
            padding: $spacing-small ($spacing-small + 5px);
        } @else {
            padding: ($spacing-small + 3px) ($spacing-small + 5px + 5px) ($spacing-small + 3px) ($spacing-small + 5px + 5px);
        }

        &:before {
            @extend %transition;
            
            content:"";
            position: absolute;
            top: calc(50% - (#{$font-size-medium}/2));
            left: calc(50% - (#{$font-size-medium}/2));
            

            display: block;
            width: $font-size-medium;
            height: $font-size-medium;
            border-radius: 50%;
            background-color: $brand-info;
        }

        .glyphicon-remove {
            font-size: $font-size-smallest;
            top: -2px;
            font-weight: bold;
        }

        &:hover, &:focus, &:active {
            background-color: transparent;
            color: #FFF;

            &:before {
                background-color: $brand-default;
            }
        }
        &:active {
            &:before {
                background-color: darken($Siemens_Blue_7, 5%);
            }
        }
    }
}

.labeled-compact-search-group {
    .form-control-toolbar {
        top: calc(1px + (1em * #{$line-height-base}) + #{$spacing-smaller});
    }
}

.inverse-compact-search-group {
    .control-label {
        color: #FFF;
    }

    .search-clear-btn {
        color: $brand-info;

        &:before {
            background-color: #FFF;
            border: 1px solid $brand-info
        }

        &:hover, &:focus, &:active {

            &:before {
                background-color: $brand-info;
                border-color: $brand-info;
            }
            
        }

        &:active {
            color: #FFF;

            &:before {
                background-color: $brand-primary;
                border-color: $brand-primary;
            }
        }
    }
}

// multiple input fields after another
.multiple-fields-form-group {
    @if ($dense-app == false) {
        margin-bottom: $spacing-medium;
    } @else {
        margin-bottom: $form-group-margin-bottom;
    }

    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -$spacing-small;

    > .control-label {
        flex-basis: 100%;
        margin-left: $spacing-small;
    }

    .form-group {
        flex: 1;
        flex-basis: 25%;
        min-width: 0;
        margin-bottom:0;
        margin-left: $spacing-small;
    }

    .validation-message-form-group {
        margin-top: 0;
    }
}

// internal label will put the label as a 'read-only' version on the right side of the input box
%form-group-internal-label,
.form-group-internal-label {
    position: relative;
    .control-label {
        font-weight: normal;
        color: $gray-medium;
        font-size: $font-size-small;

        position: absolute;
        margin-bottom:0;

        @if ($dense-app) {
            right: ($spacing-small + 1px);
        } @else {
            right: ($spacing-small + $spacing-smaller + 1px);
        }
        
        top: $spacing-small + $spacing-smaller;
    }

    .form-control {

        @if ($dense-app) {
            padding-right: ($spacing-small + 64px);
        } @else {
            padding-right: ($spacing-small + $spacing-smaller + 64px); 
        }
    }
}

// formgroup input that is only used for triggering a validation message
.validation-message-form-group {
    @if ($dense-app == false) {
        margin-top: -($spacing-medium - $spacing-smaller);
    } @else {
        margin-top: -($form-group-margin-bottom - $spacing-smaller);
    }
    .control-label {
        display: none;
    }
    .form-control {
        display: none;
    }

    &.has-error {
        &:before {
            content: none;
        }
    }
}

